import { BrowserWindow } from 'electron';

export interface FingerprintResult {
  success: boolean;
  template?: Buffer;
  imageData?: Buffer;
  error?: string;
}

export class FingerprintService {
  private mainWindow: BrowserWindow;

  constructor(mainWindow: BrowserWindow) {
    this.mainWindow = mainWindow;
  }

  /**
   * 初始化指纹板
   */
  async initializeFingerprint(): Promise<boolean> {
    try {
      // 发送消息到渲染进程初始化指纹板
      this.mainWindow.webContents.send('fingerprint:init');
      return true;
    } catch (error) {
      console.error('初始化指纹板失败:', error);
      return false;
    }
  }

  /**
   * 开始指纹采集
   */
  async startFingerprintCapture(): Promise<boolean> {
    try {
      this.mainWindow.webContents.send('fingerprint:start-capture');
      return true;
    } catch (error) {
      console.error('开始指纹采集失败:', error);
      return false;
    }
  }

  /**
   * 停止指纹采集
   */
  async stopFingerprintCapture(): Promise<void> {
    try {
      this.mainWindow.webContents.send('fingerprint:stop-capture');
    } catch (error) {
      console.error('停止指纹采集失败:', error);
    }
  }

  /**
   * 采集指纹
   */
  async captureFingerprint(): Promise<FingerprintResult> {
    return new Promise((resolve) => {
      const timeout = setTimeout(() => {
        resolve({
          success: false,
          error: 'Fingerprint capture timeout'
        });
      }, 15000); // 15秒超时

      // 监听指纹采集结果
      const handleFingerprintResult = (event: any, result: FingerprintResult) => {
        clearTimeout(timeout);
        this.mainWindow.webContents.removeListener('fingerprint:capture-result', handleFingerprintResult);
        resolve(result);
      };

      this.mainWindow.webContents.on('fingerprint:capture-result', handleFingerprintResult);
      
      // 发送指纹采集命令
      this.mainWindow.webContents.send('fingerprint:capture');
    });
  }


  /**
   * 验证指纹
   */
  async verifyFingerprint(template: Buffer): Promise<boolean> {
    return new Promise((resolve) => {
      const timeout = setTimeout(() => {
        resolve(false);
      }, 10000);

      const handleVerifyResult = (event: any, result: boolean) => {
        clearTimeout(timeout);
        this.mainWindow.webContents.removeListener('fingerprint:verify-result', handleVerifyResult);
        resolve(result);
      };

      this.mainWindow.webContents.on('fingerprint:verify-result', handleVerifyResult);
      this.mainWindow.webContents.send('fingerprint:verify', template);
    });
  }

  /**
   * 获取指纹板状态
   */
  async getFingerprintStatus(): Promise<string> {
    return new Promise((resolve) => {
      const handleStatus = (event: any, status: string) => {
        this.mainWindow.webContents.removeListener('fingerprint:status', handleStatus);
        resolve(status);
      };

      this.mainWindow.webContents.on('fingerprint:status', handleStatus);
      this.mainWindow.webContents.send('fingerprint:get-status');
    });
  }
}
