import { repo } from '../db/connection';
import * as XLSX from 'xlsx';
import * as fs from 'fs';
import * as path from 'path';

export interface CustomerImportData {
  refNo: string;
  name: string;
  address1: string;
  address2?: string;
  city: string;
  state: string;
  zipCode: string;
  telephone?: string;
}

export interface VehicleImportData {
  refNo: string;
  license: string;
  idriver?: string;
  year?: number;
  color: string;
  make: string;
  model?: string;
}

export interface ImportResult {
  success: boolean;
  message: string;
  importedCustomers: number;
  importedVehicles: number;
  errors: string[];
}

export class ImportService {
  private activationId: number;

  constructor(activationId: number) {
    this.activationId = activationId;
  }

  /**
   * 导入用户信息
   */
  async importCustomers(filePath: string): Promise<ImportResult> {
    try {
      const data = this.parseFile(filePath);
      const customerData = this.parseCustomerData(data);
      
      let importedCount = 0;
      const errors: string[] = [];

      for (const customer of customerData) {
        try {
          // 检查是否已存在相同姓名和地址的客户
          const existingCustomer = repo.customers.findByNameAndAddress(
            this.activationId, 
            customer.name, 
            this.formatAddress(customer)
          );

          if (existingCustomer) {
            console.log(`客户 ${customer.name} 已存在，跳过导入`);
            continue;
          }

          // 创建新客户
          const result = repo.customers.create(this.activationId, {
            name: customer.name,
            phone: customer.telephone,
            address: this.formatAddress(customer)
          });

          if (result.changes > 0) {
            importedCount++;
          }
        } catch (error) {
          const errorMsg = `导入客户 ${customer.name} 失败: ${error}`;
          errors.push(errorMsg);
          console.error(errorMsg);
        }
      }

      return {
        success: true,
        message: `成功导入 ${importedCount} 个客户`,
        importedCustomers: importedCount,
        importedVehicles: 0,
        errors
      };
    } catch (error) {
      return {
        success: false,
        message: `导入失败: ${error}`,
        importedCustomers: 0,
        importedVehicles: 0,
        errors: [String(error)]
      };
    }
  }

  /**
   * 导入车辆信息
   */
  async importVehicles(filePath: string): Promise<ImportResult> {
    try {
      const data = this.parseFile(filePath);
      const vehicleData = this.parseVehicleData(data);
      
      let importedCount = 0;
      const errors: string[] = [];

      for (const vehicle of vehicleData) {
        try {
          // 检查车辆是否已存在
          const existingVehicle = repo.vehicles.findByLicensePlate(
            this.activationId, 
            vehicle.license
          );

          if (existingVehicle) {
            console.log(`车辆 ${vehicle.license} 已存在，跳过导入`);
            continue;
          }

          // 尝试匹配客户
          let customerId: number | undefined;
          
          // 如果有IDriver字段，尝试根据IDriver匹配客户
          if (vehicle.idriver && vehicle.idriver.trim()) {
            const customers = repo.customers.findByName(this.activationId, vehicle.idriver.trim());
            if (customers && customers.length > 0) {
              customerId = customers[0].id;
              console.log(`车辆 ${vehicle.license} 匹配到客户: ${customers[0].name}`);
            }
          }
          
          // 创建车辆记录
          const result = repo.vehicles.create(this.activationId, {
            customer_id: customerId,
            license_plate: vehicle.license,
            year: vehicle.year,
            color: vehicle.color,
            make: vehicle.make,
            model: vehicle.model,
            original_ref_no: vehicle.refNo
          });

          if (result.changes > 0) {
            importedCount++;
          }
        } catch (error) {
          const errorMsg = `导入车辆 ${vehicle.license} 失败: ${error}`;
          errors.push(errorMsg);
          console.error(errorMsg);
        }
      }

      return {
        success: true,
        message: `成功导入 ${importedCount} 个车辆`,
        importedCustomers: 0,
        importedVehicles: importedCount,
        errors
      };
    } catch (error) {
      return {
        success: false,
        message: `导入失败: ${error}`,
        importedCustomers: 0,
        importedVehicles: 0,
        errors: [String(error)]
      };
    }
  }

  /**
   * 解析文件
   */
  private parseFile(filePath: string): any[] {
    const ext = path.extname(filePath).toLowerCase();
    
    if (ext === '.csv') {
      return this.parseCSV(filePath);
    } else if (ext === '.xlsx' || ext === '.xls') {
      return this.parseExcel(filePath);
    } else {
      throw new Error('不支持的文件格式');
    }
  }

  /**
   * 解析CSV文件
   */
  private parseCSV(filePath: string): any[] {
    const content = fs.readFileSync(filePath, 'utf-8');
    const lines = content.split('\n').filter(line => line.trim());
    
    if (lines.length < 2) {
      throw new Error('文件内容不足');
    }

    const headers = lines[0].split(',').map(h => h.trim().replace(/"/g, ''));
    const data: any[] = [];

    for (let i = 1; i < lines.length; i++) {
      const values = lines[i].split(',').map(v => v.trim().replace(/"/g, ''));
      const row: any = {};
      
      headers.forEach((header, index) => {
        row[header] = values[index] || '';
      });
      
      data.push(row);
    }

    return data;
  }

  /**
   * 解析Excel文件
   */
  private parseExcel(filePath: string): any[] {
    const workbook = XLSX.readFile(filePath);
    const sheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[sheetName];
    return XLSX.utils.sheet_to_json(worksheet);
  }

  /**
   * 解析客户数据
   */
  private parseCustomerData(data: any[]): CustomerImportData[] {
    return data.map(row => ({
      refNo: row['RefNo'] || row['refNo'] || '',
      name: row['Name'] || row['name'] || '',
      address1: row['Address1'] || row['address1'] || '',
      address2: row['Address2'] || row['address2'] || '',
      city: row['City'] || row['city'] || '',
      state: row['State'] || row['state'] || '',
      zipCode: row['ZIP Code'] || row['zipCode'] || row['ZIP'] || '',
      telephone: row['Telephone'] || row['telephone'] || ''
    }));
  }

  /**
   * 解析车辆数据
   */
  private parseVehicleData(data: any[]): VehicleImportData[] {
    return data.map(row => ({
      refNo: row['Ref. No.'] || row['RefNo'] || row['refNo'] || '',
      license: row['License'] || row['license'] || '',
      idriver: row['IDriver'] || row['idriver'] || '',
      year: row['N'] ? parseInt(row['N']) : undefined,
      color: row['Color'] || row['color'] || '',
      make: row['Make'] || row['make'] || '',
      model: row['Model'] || row['model'] || ''
    }));
  }

  /**
   * 格式化地址
   */
  private formatAddress(customer: CustomerImportData): string {
    const parts = [
      customer.address1,
      customer.address2,
      customer.city,
      customer.state,
      customer.zipCode
    ].filter(part => part && part.trim());
    
    return parts.join(', ');
  }
}
